<template>
  <div>
     <div class="showsur">
        <h3>用户管理系统</h3>
        <input type="text" placeholder="搜索" class="search" v-model='serch' autocomplete="false" disableautocomplete />
      
        <div class="heads" style="white-space: nowrap;overflow: hidden;">
          <span>姓名</span>
          <span>电话</span>
          <span>学历</span>
        </div>
        <ul v-for="sos in fifls">
          <li class="bods">
            <span>{{sos.name}}</span>
            <span>{{sos.phone}}</span>
            <span>{{sos.schooling}}</span>
            <router-link v-bind:to="/wei/+sos.id" tag="label" class="lost">详情</router-link>
          </li>
        </ul>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'

    export default{
      data(){
        return{
          serch:''
        }
      },
      computed:{
        //搜索框
        fifls(){
          return this.$store.state.products.filter((blo) =>{
                 return blo.name.match(this.serch);
                
          })
        }
      }
    }
    
</script>
<style scoped>
  ul li{
    list-style-type: none;
    border-top: 1px solid #ccc;
    white-space: nowrap;
    overflow: hidden;
  }
/*奇偶*/
 /* ul li:nth-child(odd){
    background: #eee;
  }
  ul li:nth-child(2n){
    background: black;
  }*/
  
  .tishi{
    margin: 0 auto;
    background: yellow;
    width: 600px;
    height: 25px;
    text-align: center;
    line-height: 25px;
  }
  .tishi span{float: right;right: 0;cursor: pointer;}
  .heads span{
    display: inline-block;
    width: 150px;
    font-size: 14px;
    font-weight: bold;
  }
  ul li span{
    display: inline-block;
    width: 150px;
    line-height: 30px;
    font-size: 12px;
  }
    h3{
    border-bottom: 1px dashed #eee;
    padding: 5px;
    margin-bottom: 10px;
  }
  .showsur{
    max-width: 600px;
    margin: 50px auto;
    margin-bottom: 5px;
  }
  .search{
    width: 100%;
    padding: 2px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  .lost{
    border: 1px solid #bbb;
    border-radius: 3px;
    padding: 2px 7px;
    font-size: 14px;
    cursor: pointer;
  }
  .lost:hover{
    background: #ddd;
  }
</style>




