<template>
        <div>
        <div class="xiangq">
            <router-link class="headf" to="/" tag="div">返回</router-link>
            <dir class="titl">
                <b>{{sss.name}}</b>
                <router-link v-bind:to="'/bian/' + sss.id" tag="label" class="an">编辑</router-link>
                <button class="an" v-on:click="deleats(sss.id)">删除</button>
            </dir>
            <ul>
                <li><span>$电话</span>{{sss.phone}}</li>
                <li><span>$邮箱</span>{{sss.email}}</li>
                <li><span>$毕业学校</span>{{sss.school}}</li>
                <li><span>$学历</span>{{sss.schooling}}</li>
                <li><span>$职业</span>{{sss.age}}</li>
                <li><span>$个人简历</span>{{sss.companyId}}</li>
            </ul>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
    export default{
        data(){
            return{
               sss:""
            }
        },
        methods:{
            sou(e){
                this.$store.state.products.map( data =>{
                    if(data.id == e){
                        this.sss = data;
                    }
                });
            },
            deleats(id){       
                this.$store.commit('deleat',id);
                this.$router.push('/');
            }
        },
        created(){
            this.sou(this.$route.params.id);
        }
    }
</script>
<style scoped>
    *{
    	margin: 0;
    	padding: 0;
    }
    .headf{
    	width:35px;
    	height: 18px;
    	border: 1px solid #ddd;
    	border-radius: 4px;
    	font-size: 14px;
    	text-align: center;
    	line-height: 18px;
    	margin-bottom: 20px;
    	cursor: pointer;
    }
    .headf:hover{
        background: #eee;
    }
    ul li{
    	list-style-type: none;
    	border: 1px solid #ddd;
    	width: 100%;
    	line-height: 22px;
    	font-size: 12px;
    	margin-bottom: 10px;
    	word-wrap: break-word;
    }
    span{
    	font-weight: bold;
    	margin-left: 5px;
    	margin-right: 5px;
    }
    b{
    	font-size: 16px;
    }
    .an{
    	float: right;
    }
    label{
    	background: blue;
    	border-radius: 4px;
    	padding: 5px 7px;
    	font-size: 14px;
    	color: white;
    	cursor: pointer;
    }
    button{
    	background: red;
    	padding: 5px 7px;
    	color: white;
    	border: none;
    	border-radius: 4px;
    	font-size: 14px;
    	cursor: pointer;
    }
    .xiangq{
    	max-width: 600px;
    	margin: 30px auto;
    }
	.titl{
    border-bottom: 1px dashed #eee;
    padding: 5px;
    margin-bottom: 10px;
    width: 100%;
    }
</style>


