<template>
  <div id="app">
    <header>
      <span class="i">用户管理系统</span>
      <router-link to="/" tag="span" class="rspan">主页</router-link>
      <router-link to="/fcosewe" tag="span" class="rspan">关于我们</router-link>
      <router-link to="/tian" tag="span" class="lspan">添加用户</router-link>
    </header>
    <router-view></router-view>
    
  </div>
</template>

<script>
  
export default {
  name: 'App',
  created(){
    window.addEventListener('unload',this.saveState)
  },
  methods:{
    saveState(){
      sessionStorage.setItem('state',JSON.stringify(this.$store.state))
    }
  }
}
</script>

<style>
  *{
    margin: 0;
    padding: 0;
  }
  header{
    width: 100%;
    height: 26px;
    background: #eee;
  }
  .i{
    font-size: 14px;
    margin-left: 50px; 
  }
  .rspan{
    font-size: 12px;
    margin-left: 10px;
    cursor: pointer;
  }
  .lspan{
    font-size: 12px;
    position: absolute;
    right: 50px;
    line-height: 26px;
    cursor: pointer;
  }
</style>



