<template>
	<dir>
		<div class="tianj">
			<h3>添加用户</h3>
				<!--这里给他绑定个submit方法，就可以用自己的submit方法，不添加就用默认的-->
				<form v-on:submit='addCustome'>	
					<div class="shows">
						<ul>
							<li><p>用户信息</p></li>
							<li>
								<p><em style="color: red;">* </em>姓名</p>
								<input type="text" maxlength="10" v-model="custom.name" autocomplete="false" disableautocomplete />
							</li>
							<li>
								<p><em style="color: red;">* </em>电话</p>
								<input type="text" maxlength="35" v-model="custom.phone" autocomplete="false" disableautocomplete />
							</li>
							<li>
								<p>邮箱</p>
								<input type="text" maxlength="35" v-model="custom.emali" autocomplete="false" disableautocomplete />
							</li>
							<li>
								<p><em style="color: red;">* </em>学历</p>
								<input type="text" maxlength="35" v-model="custom.schooling" autocomplete="false" disableautocomplete />
							</li>
							<li>
								<p>毕业学校</p>
								<input type="text" maxlength="35" v-model="custom.school" autocomplete="false" disableautocomplete />
							</li>
							<li>
								<p>职业</p>
								<input type="text" maxlength="35" v-model="custom.professional" autocomplete="false" disableautocomplete />
							</li>
							<li>
								<p>个人简历</p>
								<textarea v-model="custom.resume"></textarea>
							</li>
						</ul>
					</div>
					<button type="submit">添加</button>
				</form>
		</div>
	</dir>
</template>
<script>
  import axios from 'axios'
	export default{
		data(){
			return{
				custom:{}
			}
		},
		methods:{
			isNumber(value){	
			     var patrn = /^(-)?\d+(\.\d+)?$/;
			     if (patrn.exec(value) == null || value == "") {
			        return false
			     } else {
			         return true
			     }
			},
			addCustome(e){
				if(!this.custom.name || !this.custom.phone || !this.custom.schooling){
					alert('请添加对应的信息！');
				}else if(!this.isNumber(this.custom.phone)){
					alert('电话号码只能为数字');
				}else{
					//把数据封装成一个对象
					var newCustome = {
						name:this.custom.name,
						phone:this.custom.phone,
						email:this.custom.emali,
						school:this.custom.school,
						schooling:this.custom.schooling,
						age:this.custom.professional,
						companyId:this.custom.resume
					}
					this.$store.commit('fuid',newCustome);
					this.$router.push('/');
					//把这对象传到本地服务器上
				/*	axios.post('http://localhost:3000/user',newCustome)
					.then((data) =>{

						//添加完成后跳转到主页去,query可以传东西过去
						this.$router.push({path:'/',query:{aler:"用户添加完成！"}});
					})*/
					//取消其被动
					e.preventDefault();
					}
				e.preventDefault();
			}
		}
	}
</script>
<style scoped>
    *{
    	margin: 0;
    	padding: 0;
    }
    button{
    	padding: 5px 7px;
    	border-radius: 4px;
    	color: white;
    	font-size: 12px;
    	background: blue;
    	border: none;
    	margin-top: 10px;
    	cursor: pointer;
    }
    p{
    	font-size: 12px;
    	font-weight: bold;
    	margin-left: 5px;
    }
    textarea{
    	width:95%;
    	height: 100px;
    	margin-left: 13px;
    	margin-bottom: 10px;
    }
    input{
    	width: 95%;
    	margin-left: 13px;
    	line-height: 18px;
    }
    ul li{
    	list-style-type: none;
    	margin-top: 10px;
    }
	.tianj{
		max-width: 600px;
		margin: 50px auto;
	}
	h3{
	    border-bottom: 1px dashed #eee;
	    padding: 5px;
	    margin-bottom: 10px;
  }
  .shows{
	  	width: 100%;
	  	border: 1px solid #ddd;
	  	background:#eee;
  }
</style>


