import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
export const store = new Vuex.Store({
	//配合app.vue里的一起，把state里的数据传到sessionStorage里去，等页面刷新后再从里面拿出来
	state:sessionStorage.getItem('state') ? JSON.parse(sessionStorage.getItem('state')): {
		products:[
			{name:'叶洛',phone:156132486,schooling:"华南大学",id:5},
			{name:'南宫',phone:13248975615,schooling:'东南大学',id:16},
			{name:'宇天',phone:48943519599,schooling:"华北大学",id:3}
		]
	},
	getters:{
		
	},
	mutations:{
		//添加及赋予Id值
		fuid:(data,arr)=>{
			let dius = data.products;
			let or = 0;
			for(let j=0;j<dius.length;j++){
				dius[j].id = j;
				or = j;
			}
			arr.id = or+1;
			dius.push(arr);
		},
		//删除指定数据
		deleat(state,id){
			let arr = state.products;
			let chan = 0;
			for(let i=0;i<arr.length;i++){
				if(arr[i].id == id){
					chan = i;
				}
			}			
			arr.splice(chan,1);
		},
		chang(state,cha){
			//修改
				let arr = state.products;
				let chan = null;
	  			for(let i=0;i<arr.length;i++){
	  				if(arr[i].id == cha.id){
	  					chan = i;
	  				}	
	  			}
	  		arr.splice(chan,1,cha);
		}
	}
})


