// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import Router from 'vue-router'
import {store} from './store/store'
import Fcosewe from './components/fcosewe'
import Tian from './components/tian'
import Home from './components/home'
import Wei from './components/xiang/wei'
import Bian from './components/xiang/bian'


Vue.config.productionTip = false
Vue.use(Router)

const routes=[
	{path:"/",component:Home},
	{path:"/tian",name:"tian",component:Tian},
	{path:"/fcosewe",name:"fcosewe",component:Fcosewe},
	{path:"/wei/:id",component:Wei},
	{path:"/bian/:id",component:Bian}

]
const router = new Router({
	routes,
	mode:'hash'
})

/*const router = new Router({
	routes:[
	{path:'/',component:Home},
	{path:'/tian',name:"tian",component:Tian},
	{path:'/fcosewe',name:"fcosewe",component:Fcosewe},
	{path:'/wei/:id',component:Wei},
	{path:'/bian',component:Bian}
	],
	mode:'history'
})*/

//过滤器
Vue.filter('guolv',function(vuel){
	return vuel.slice(0,10) + '...';
})

new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
})



