<template>
	<dir>
		<div class="tianj">
			<h3>编辑用户</h3>
				<div class="shows">
					<ul>
						<li><p>用户信息</p></li>
						<li>
							<p><em style="color: red;">* </em>姓名</p>
							<input type="text" maxlength="10" v-model="cutom.name" autocomplete="false" disableautocomplete />
						</li>
						<li>
							<p><em style="color: red;">* </em>电话</p>
							<input type="text" maxlength="35" v-model="cutom.phone" autocomplete="false" disableautocomplete />
						</li>
						<li>
							<p>邮箱</p>
							<input type="text" maxlength="35" v-model="cutom.email" autocomplete="false" disableautocomplete />
						</li>
						<li>
							<p><em style="color: red;">* </em>学历</p>
							<input type="text" maxlength="35" v-model="cutom.schooling" autocomplete="false" disableautocomplete />
						</li>
						<li>
							<p>毕业学校</p>
							<input type="text" maxlength="35" v-model="cutom.school" autocomplete="false" disableautocomplete />
						</li>
						<li>
							<p>职业</p>
							<input type="text" maxlength="35" v-model="cutom.age" autocomplete="false" disableautocomplete />
						</li>
						<li>
							<p>个人简历</p>
							<textarea v-model="cutom.companyId"></textarea>
						</li>
					</ul>
				</div>
				<button @click='ti'>完成</button>
		</div>
	</dir>
</template>
<script>
import axios from 'axios'
	export default{
		data(){
			return{
				cutom:{},
				iid:0
			}
		},
		methods:{
			los(id){
				this.$store.state.products.map( data =>{
					if(data.id == id){
						//用深度克隆，将其赋予到cutom,如果直接赋予给cutom，那其实引用值，他改变会直接一起改变
						this.cutom = JSON.parse(JSON.stringify(data));
					}
				})
				this.iid = id;
			},
			isNumber(value){	
			     var patrn = /^(-)?\d+(\.\d+)?$/;
			     if (patrn.exec(value) == null || value == "") {
			        return false
			     } else {
			         return true
			     }
			},
			ti(e){
				if(!this.cutom.name || !this.cutom.phone || !this.cutom.schooling){
					alert('请输入对应的内容');
				}else if(!this.isNumber(this.cutom.phone)){
					alert('电话号码只能为数字');
				}else{
					var newtome = {
						name:this.cutom.name,
						phone:this.cutom.phone,
						email:this.cutom.email,
						school:this.cutom.school,
						schooling:this.cutom.schooling,
						age:this.cutom.age,
						companyId:this.cutom.companyId,
						id:this.iid
					}
						this.$store.commit('chang',newtome);
						this.$router.push('/');
					}
			}

		},
		created(){
			this.los(this.$route.params.id);
		}
	}
</script>
<style scoped>
    *{
    	margin: 0;
    	padding: 0;
    }
    button{
    	padding: 5px 7px;
    	border-radius: 4px;
    	color: white;
    	font-size: 12px;
    	background: blue;
    	border: none;
    	margin-top: 10px;
    	cursor: pointer;
    }
    p{
    	font-size: 12px;
    	font-weight: bold;
    	margin-left: 5px;
    }
    textarea{
    	width:95%;
    	height: 100px;
    	margin-left: 13px;
    	margin-bottom: 10px;
    }
    input{
    	width: 95%;
    	margin-left: 13px;
    	line-height: 18px;
    }
    ul li{
    	list-style-type: none;
    	margin-top: 10px;
    }
	.tianj{
		max-width: 600px;
		margin: 50px auto;
	}
	h3{
	    border-bottom: 1px dashed #eee;
	    padding: 5px;
	    margin-bottom: 10px;
  }
  .shows{
	  	width: 100%;
	  	border: 1px solid #ddd;
	  	background:#eee;
  }
</style>


